.project-heading{
    text-align:center;
    padding: 4rem 0 2rem 0;

}
.project-container{
    max-width: 1140px;
    margin:auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;

}
.project-card{
    background: #1a1919;
    padding: 1.2rem 1rem;

}
.project-card img{
    width: 100%;
}
.project-title{
    color: #fff;
    padding: 1rem 0;
}
.pro-details p{
    padding-bottom: 1rem;
    font-size:1.1rem;
    /* text-align: justify; */
    text-align: left;
}
.pro-btns{
    display: flex;
    justify-content: 90%;
    padding: 0.5rem .2rem;
}
/* view and source buttons for projects */
.pro-btns .btn{
    padding: 0.4rem 1rem;
    padding-block: .2rem;
}
@media screen and (max-width: 740px) {
    .project-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
    
}
